import React from "react";
import { useHistory, NavLink } from "react-router-dom";

// import brandingLogo from "../../components/codexlogo.png";
import "./editor.css";

import { makeStyles, createStyles, Theme } from "@material-ui/core";
import { ThemeProvider } from "@material-ui/styles";
import { darkTheme } from "../../components/MaterialTheming";

import EditorBody from "./components/EditorBody";

import firebase from "../../components/firebase.js";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    editorPage: {
      height: "100%",
      width: "100%",
      display: "grid",
      gridGap: "14px",
      gridTemplateRows: "auto 1fr"
    },
    brandingLogo: {
      cursor: "pointer",
      display: "flex",
      justifyContent:"start",
      alignItems:"center",
      gap: "5px",
      color: "#2f9bff",
      fontSize:"26px",
      textDecoration:"none"
    },
    header: {
      display: "grid",
      gridTemplateColumns: "repeat(3,1fr)",
      "& > *": {
        margin: "auto 0"
      }
    },
    codeTitle: {
      color: "#2196F3",
      backgroundColor: "transparent",
      border: "none",
      outline: "none",
      textAlign: "center",
      height: "100%",
      width: "100%"
    },
    body: {
      height: "100%",
      width: "100%",
      display: "grid",
      gridTemplateRows: "70% 30%"
    }
  })
);

function Editor(props) {
  const classes = useStyles();
  const history = useHistory();
  const [className, setClassName] = React.useState(""),
    [notOwner, setNotOwner] = React.useState(false);

  // let notOwner = true;

  // function setNotOwner(bool) {
  //   notOwner = bool;
  // }

  React.useEffect(() => {
    firebase
      .database()
      .ref("CodeX/" + props.match.params.editorID + "/className")
      .once("value")
      .then((snap) => {
        setClassName(snap.val());
      });
    if (
      localStorage.getItem("codex-codes") &&
      JSON.parse(localStorage.getItem("codex-codes"))[
        props.match.params.editorIndex
      ].key === props.match.params.editorID
    )
      setNotOwner(true);
  }, []);

  React.useEffect(() => {
    if (className.trim() !== "" && notOwner) {
      firebase
        .database()
        .ref("CodeX/" + props.match.params.editorID + "/className")
        .set(className);

      console.log(localStorage.getItem("codex-codes"));
      if (localStorage.getItem("codex-codes")) {
        let classNames = JSON.parse(localStorage.getItem("codex-codes"));
        classNames[props.match.params.editorIndex].name = className;
        localStorage.setItem("codex-codes", JSON.stringify(classNames));
      }
    }
  }, [className]);

  return (
    <ThemeProvider theme={darkTheme}>
      <div className={classes.editorPage}>
        <div className={classes.header}>
          <NavLink to="/" className={classes.brandingLogo}>
            <svg xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 640 512" fill="#2f9bff"><path d="M392.8 1.2c-17-4.9-34.7 5-39.6 22l-128 448c-4.9 17 5 34.7 22 39.6s34.7-5 39.6-22l128-448c4.9-17-5-34.7-22-39.6zm80.6 120.1c-12.5 12.5-12.5 32.8 0 45.3L562.7 256l-89.4 89.4c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0l112-112c12.5-12.5 12.5-32.8 0-45.3l-112-112c-12.5-12.5-32.8-12.5-45.3 0zm-306.7 0c-12.5-12.5-32.8-12.5-45.3 0l-112 112c-12.5 12.5-12.5 32.8 0 45.3l112 112c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L77.3 256l89.4-89.4c12.5-12.5 12.5-32.8 0-45.3z"/></svg>
            <span>CodeCompiler</span>
          </NavLink>
          <input
            value={className}
            onChange={(e) => {
              setClassName(e.target.value);
            }}
            className={classes.codeTitle}
            spellCheck={false}
            readOnly={!notOwner}
          />
        </div>
        <EditorBody
          storeAt={"CodeX/" + props.match.params.editorID}
          index={props.match.params.editorIndex}
        />
      </div>
    </ThemeProvider>
  );
}

export default Editor;
